<template>
  <div class="exam">
    <h3 v-if='submit'>ËÄÉËØïÂ∑≤ÂÆåÊàê</h3>
    <h3 v-if='!submit'>ËÄÉËØï‰∏≠...</h3>

    <div class="container papper">
      <ExamPapper @change='change' :submit='submit'/>
    </div>

    <div class="container button"  v-if="!submit">
      <input type="button" class="primary-button" @click="check" value="ÂÅöÂÆå‰∫Ü">
    </div>

    <div class="container result" v-if="submit">
      <p>ËÄÉËØïÂæóÂàÜÔºö{{resultMessage}}<p>
      <h3 v-if="score===100">‰∏çÈîôÂì¶ÔºÅ üêÇ</h3>
      <h3 v-if="score>=80&&score<100">Âä†Ê≤πÔºåÂÜçÁªÜÂøÉ‰∏ÄÁÇπÔºå‰Ω†ÂèØ‰ª•ÂæóÂà∞100ÂàÜÁöÑÔºÅ</h3>
      <h3 v-if="score>60&&score<80">ÊúâÁÇπÈöæÂ∫¶ÂêßÔºå‰∏çÊúçÊ∞îÂ∞±<router-link to="/menu"> ÁÇπÊàë </router-link>ÂÜçÊù•‰∏ÄÈÅç„ÄÇ</h3>
      <h3 v-if="score<60">Â§±Ë¥•ÊòØÊàêÂäü‰πãÊØçÔºåÈîôÈ¢òÊòØÊúÄÂ•ΩÁöÑËÄÅÂ∏àÔºåÊù•ÁúãÁúãÊàë‰ª¨ÈîôÂú®Âì™ÂÑø‰∫ÜÂêß„ÄÇ<p/>‰∏çÊúçÊ∞îÂ∞±<router-link to="/menu"> ÁÇπÊàë </router-link>ÂÜçÊù•‰∏ÄÈÅç!</h3>  
    </div>
  </div>
</template>

<script>
import ExamPapper from "./ExamPapper.vue";

export default {
  components: {
    ExamPapper
  },

  data: function() {
    return {
      resultMessage: null,
      score: 0,
      submit: false
    };
  },

  methods: {
    change(res) {
      // console.log(JSON.stringify(res));
      this.score = Math.round(parseInt(res.score)*100/parseInt(res.total));
    },

    check: function() {
      this.submit = false;
      this.resultMessage = "";

      if(! this.inputAllFilled()) {
          alert("‰Ω†ËøòÊ≤°ÊúâÂÅöÂÆåÂë¢ ÔºÅÔºÅÔºÅ");
          return;
      }

      this.resultMessage = this.score + "ÂàÜ";
      this.submit = true;
      this.disableAllInputBox();
    },

    inputAllFilled() {
      var boxes = document.getElementsByClassName("input");
      for (var i = 0; i < boxes.length; i++) {
          var value = boxes[i].value;
          if( ! value ) {
            return false;
          }
      }

      return true;
    },

    disableAllInputBox() {
      var boxes = document.getElementsByClassName("input");
      for (var i = 0; i < boxes.length; i++) {
          boxes[i].readOnly = true;
      }
    }
  }
};
</script>

